import { Fragment, useState, useEffect } from "react";
import { connect } from "react-redux";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col } from "antd";
import {
  departmentAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Departments/departmentSlice";


const AddDepartment = (props) => {
  const [form] = Form.useForm();
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.selectedData?.name,


    };
    if (obj.name != null || obj.name == "") {
      props.departmentAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const departmentToUpdate = {
      id: props.selectedData.id,
      name: props.selectedData?.name,
    };
    props.departmentAdd(departmentToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.selectedData.id == null || props.selectedData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.selectedData.id == null || props.selectedData.id == 0
        ? "Add"
        : "Update"}{" "}
      Department
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.selectedData.name
    })
  }, [props.selectedData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.selectedData.id == null) {
            handleAdd();
          }
          else {
            handleUpdate();
          }
        }}>
        <Form.Item label="Department Name :"
          name="name"
          rules={[
            { required: true, message: "Please input your Department Name!" },
            {
              pattern: new RegExp(
                /^[a-zA-Z][a-zA-Z\s]+$/i
              ),
              message: "field does not accept numbers",
            }
          ]}
          hasFeedback>
          <Input
            id="Departments"
            name="name"
            style={{ width: "100%" }}
            placeholder="Department Name"
            value={props.selectedData?.name}
            onChange={ChangeHandeler}
          />
        </Form.Item>


      </Form>
    </Modal>
  );
};

function mapStateToProps({ departments }) {
  return {
    selectedData: departments.rowdata,
    model: departments.model,
  };
}

export default connect(mapStateToProps, {
  departmentAdd,
  isModelVisible,
  InputChangeValue,
})(AddDepartment);
