import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import AddDepartment from "./addDepartment";

import {
  getDepartments,
  departmentEdit,
  departmentDelete,
  isModelVisible,
  SetDepartmentsParams,
} from "../../../../store/Masters/Departments/departmentSlice";

function Departmemnt(props) {
  useEffect(() => {
    props.getDepartments({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Departments",
      dataIndex: "name",
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_department") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.departmentEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}
          {props.userAccessList?.indexOf("Masters.delete_department") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.departmentDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
          )}
        </Space>
      ),
    },
  ];

  const data = props.departmentslist.map((row, key) => ({
    ...row,
    index:
      (props.departmentsParams.page - 1) * props.departmentsParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.SetDepartmentsParams({
      ...props.departmentsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getDepartments({
      ...props.departmentsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetDepartmentsParams({ ...props.departmentsParams, search: val });
    props.getDepartments({ ...props.departmentsParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Departments Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_department") !== -1 &&
          "Add Departments"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-16">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getDepartments({
                      ...props.departmentsParams,
                      page: 1,
                      search: "",
                    });
                    props.SetDepartmentsParams({
                      ...props.departmentsParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.departmentsParams?.page}
                pageSize={props.departmentsParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddDepartment />
    </Row>
  );
}

function mapStateToProps({ departments, system }) {
  return {
    departmentslist: departments.departmentslist,
    noofpages: departments.noofpages,
    listCount: departments.listCount,
    departmentsParams: departments.departmentsParams,
    loading: departments.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getDepartments,
  departmentEdit,
  departmentDelete,
  isModelVisible,
  SetDepartmentsParams,
})(Departmemnt);
